export default {
	/* 
		1、 除了父传子可以使用props接收外，navigate组件跳转
				传递过来的参数,props也可以进行接收，例如这里是从
				changeInfo.vue的/pages/modify/modify?title=userName
				接收过来的title参数
		2、	navigate组件跳转传递过来的参数,除了props可以进行接收,
				onLoad函数的形式参数也可以进行接收，如下
	 */
	//props:['title'],
	data() {
		return {
			// 用户的id
			currentId: "",
			// 判断用户是否修改id
			isIdChange: false,
			
			newName: '',
			newId: '',
			intrductionText: '',
			saveStyle: 'background: #55A532;',
			title: '',
			text: ''
		}
	},
	// 下面这种做法记住
	onLoad(res) {
		/*
			当页面开始加载的时候，判断缓存中是否有userId,
			有的话，就使用缓存中的userId,没有的话，就使用
			newId
		 */
		uni.getStorage({
				key: 'userId',
				success: (res) => {
						//如果用户没有修改抖音id,则用户的抖音id还是之前存储在缓存中的
						if(this.isIdChange === false) {
							this.currentId = res.data;
						}
				}
		});
		//this.userId = 
		
		switch (res.title){
			case 'userName':
				this.title = '修改昵称';
				this.text = '我的昵称';
				break;
			case 'douyinId':
				this.title = '修改抖音号';
				this.text = '我的抖音号';
				break;
			case 'introduct':
				this.title = '修改简介';
				this.text = '个人简介';
				break;
			default:
				break;
		}
	},
	methods: {
		clearAll(res) {
			switch (res){
				case this.newName:
					this.newName = '';
					break;
				case this.newId:
					this.newId = '';
					break;
				default:
					break;
			}
		},
		save(res, column) {
			if(column === "userName") {
				this.saveStyle = 'background: #cccccc;'
			};
			//判断用户是否修改抖音id，是的话, 将isIdChange设置为true
			if(column === "userId") {
				this.isIdChange = true;
				//如果用户修改了id，则currentId便是用户当前新修改的id
				this.currentId = res;
			}
			
			uni.setStorage({
				key: column,
				data: res,
				success: () => {
					uni.navigateTo({
						url: "/pages/changeInfo/changeInfo"
					})
				}
			});
			uni.request({
				url: "http://localhost:8088/user/update",
				method: "Post",
				data: {
					data: res,
					column: column,
					currentId: this.currentId
				}
			});
		}
	}
}
